div.color {
  top: 10px; }
  div.color .color-panel li {
    width: 50px; }
  div.color .color-panel li[pop-content^="#"]:hover::after {
    content: attr(pop-content);
    position: absolute;
    left: 4rem;
    bottom: 3.5em;
    min-width: 8rem;
    border: 0.5px solid rgba(250, 250, 250, 0.8);
    border-radius: 0.4rem;
    background-color: rgba(200, 200, 200, 0.3);
    padding: 1rem;
    color: white;
    font-size: 1rem;
    z-index: 1; }
  div.color .color-panel li.spliter {
