    border-color: currentColor;
    padding-top: 6px;
    margin-bottom: 6px; }
  div.color div.color-name {
    margin-top: 2rem; }
    div.color div.color-name .color-name-kanji {
      width: 4rem;
      word-wrap: break-word;
      text-align: center; }
    div.color div.color-name .color-name-roma {
      width: 4rem;
      padding: 0 auto;
      text-align: center; }

li.color {
  font-display: swap; }
